import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import { paramCase } from 'change-case';
import { useRef, useState } from 'react';
import editFill from '@iconify/icons-eva/edit-fill';
import { Link as RouterLink } from 'react-router-dom';
import trash2Outline from '@iconify/icons-eva/trash-2-outline';
import moreVerticalFill from '@iconify/icons-eva/more-vertical-fill';
// material
import { Menu, MenuItem, IconButton, ListItemIcon, ListItemText } from '@mui/material';
import { useSnackbar } from 'notistack';
import Request from '../../../../utils/Request';
// routes
import { PATH_DASHBOARD } from '../../../../routes/paths';
import useAuth from '../../../../hooks/useAuth';
import { handlePermission } from '../../../../utils/handlePermission';

// ----------------------------------------------------------------------

TirageMoreMenu.propTypes = {
  onDelete: PropTypes.func,
  tirageId: PropTypes.number
};

export default function TirageMoreMenu({ onDelete, tirageId }) {
  const ref = useRef(null);
  const { user } = useAuth();
  const [isOpen, setIsOpen] = useState(false);
  const { enqueueSnackbar } = useSnackbar();

  const handleDelete = async () => {
    setIsOpen(false);
    await Request.handleDeleteTirage(tirageId);
    onDelete();
    enqueueSnackbar('delete with success', { variant: 'success' });
  };

  return (
    <>
      <IconButton ref={ref} onClick={() => setIsOpen(true)}>
        <Icon icon={moreVerticalFill} width={20} height={20} />
      </IconButton>

      <Menu
        open={isOpen}
        anchorEl={ref.current}
        onClose={() => setIsOpen(false)}
        PaperProps={{
          sx: { width: 200, maxWidth: '100%' }
        }}
        anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
        transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      >
        {handlePermission(user?.perms, 'change_tirage') && (
          <MenuItem
            component={RouterLink}
            to={`${PATH_DASHBOARD.tirage.root}/${tirageId}/edit`}
            sx={{ color: 'text.secondary' }}
          >
            <ListItemIcon>
              <Icon icon={editFill} width={24} height={24} />
            </ListItemIcon>
            <ListItemText primary="Edit" primaryTypographyProps={{ variant: 'body2' }} />
          </MenuItem>
        )}
        {handlePermission(user?.perms, 'delete_tirage') && (
          <MenuItem
            onClick={() => {
              const confirmation = window.confirm('Êtes-vous sûr de bien vouloir supprimer?');

              if (confirmation) {
                // Perform the deletion logic here
                // You can call your API or update state as needed.
                handleDelete();
              }
            }}
            sx={{ color: 'text.secondary' }}
          >
            <ListItemIcon>
              <Icon icon={trash2Outline} width={24} height={24} />
            </ListItemIcon>
            <ListItemText primary="Supprimer" primaryTypographyProps={{ variant: 'body2' }} />
          </MenuItem>
        )}
      </Menu>
    </>
  );
}
