import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import searchFill from '@iconify/icons-eva/search-fill';
import trash2Fill from '@iconify/icons-eva/trash-2-fill';
import roundFilterList from '@iconify/icons-ic/round-filter-list';
// material
import { styled } from '@mui/material/styles';
import {
  Box,
  Toolbar,
  Tooltip,
  IconButton,
  Typography,
  OutlinedInput,
  InputAdornment,
  Stack,
  TextField
} from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { DatePicker } from '@mui/x-date-pickers';

// ----------------------------------------------------------------------

const RootStyle = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 3)
}));

const SearchStyle = styled(OutlinedInput)(({ theme }) => ({
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter
  }),
  '&.Mui-focused': { width: 300, boxShadow: theme.customShadows.z8 },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `${theme.palette.grey[500_32]} !important`
  }
}));

// ----------------------------------------------------------------------

TirageListToolbar.propTypes = {
  filterName: PropTypes.string,
  onFilterName: PropTypes.func,
  value: PropTypes.string,
  value1: PropTypes.string,
  onChangeDate1: PropTypes.func,
  onChangeDate2: PropTypes.func,
  loading: PropTypes.bool,
  setLoading: PropTypes.func,
  handleGo: PropTypes.func
};

export default function TirageListToolbar({
  onChangeDate1,
  onChangeDate2,
  handleGo,
  value,
  value1,
  loading,
  setLoading,
  filterName,
  onFilterName
}) {
  return (
    <Stack spacing={2} direction={{ xs: 'column', md: 'row' }} sx={{ py: 2.5, px: 3 }}>
      <DatePicker
        label="Date de début"
        value={value}
        inputFormat="yyyy-MM-dd"
        onChange={(newValue) => {
          onChangeDate1(newValue);
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            fullWidth
            sx={{
              maxWidth: { md: 240 }
            }}
          />
        )}
      />
      <DatePicker
        label="Date de fin"
        value={value1}
        inputFormat="yyyy-MM-dd"
        onChange={(newValue) => {
          onChangeDate2(newValue);
        }}
        renderInput={(params) => (
          <TextField
            {...params}
            fullWidth
            sx={{
              maxWidth: { md: 240 }
            }}
          />
        )}
      />

      <SearchStyle
        value={filterName}
        onChange={onFilterName}
        placeholder="Rechercher un Tirage?..."
        startAdornment={
          <InputAdornment position="start">
            <Box component={Icon} icon={searchFill} sx={{ color: 'text.disabled' }} />
          </InputAdornment>
        }
      />

      <Tooltip title="Filter list">
        <LoadingButton sx={{ py: 2, px: 3 }} loading={loading} variant="contained" onClick={handleGo}>
          Filtrer
        </LoadingButton>
      </Tooltip>
    </Stack>
  );
}
